export default class AnimationObject {
  constructor( pixiDisObj ) {
    if ( typeof pixiDisObj === 'undefined' ) {
      throw new Error('pixiDisObj');
    }

    //PIXIJS
    this.pixiDisObj = pixiDisObj;

    this.flags = {
      //
      update: true,
      //
      exist: true,
    };
  }

  //
  update() {
    throw new Error('update');
  }
}  