//IntersectionObserver
import IntersectionObserverPolyfill from 'intersection-observer';

const target = {
  //
  elements: [],
  //
  add: function( selector = '' ) {
    this.elements = this.elements.concat( [].slice.call( document.querySelectorAll( selector ) ) );
  },
};

